.timeline-section {
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
  padding: 4rem 2rem;
  color: white;
}

.timeline-title {
  text-align: right;
  font-size: 1.8rem;
  color: white;
  margin-bottom: 0.5rem;
  padding-right: 1rem;
}

.section-divider {
  height: 4px;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto 2.5rem;
  border-radius: 999px;
  background: linear-gradient(to right, #9c27ff 80%, #f39c12 20%);
}

.timeline-tabs {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 4rem;
}

.timeline-tabs button {
  padding: 0.7rem 2rem;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: 600;
  background-color: #2b2b2f;
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow:
    0 8px 18px rgba(0, 0, 0, 0.6),
    inset 0 2px 4px rgba(255, 255, 255, 0.04),
    inset 0 -2px 4px rgba(0, 0, 0, 0.4);
}

.timeline-tabs button:hover {
  background-color: #3a3a3e;
  transform: translateY(-2px);
}

.timeline-tabs .active {
  background-color: #9c27ff;
  color: #fff;
  transform: translateY(-2px);
  box-shadow:
      0 12px 22px rgba(0, 0, 0, 0.8),
      inset 0 2px 6px rgba(255, 255, 255, 0.05),
      inset 0 -2px 6px rgba(0, 0, 0, 0.5);
}

.timeline-wrapper {
  position: relative;
  padding: 2rem 0;
}

.timeline-line {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 3px;
  background-color: #444;
  transform: translateX(-50%);
  z-index: 1;
}

.timeline-item {
  position: relative;
  width: 50%;
  padding: 2rem;
  box-sizing: border-box;
}

.timeline-item.left {
  left: 0;

}

.timeline-item.right {
  left: 50%;

}

.timeline-icon {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 49px;
  height: 50px;
  background: radial-gradient(circle at center, #2a2a33, #1b1b1f);
  border-radius: 50%;
  padding: 7px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
  box-shadow:
    0 10px 25px rgba(0, 0, 0, 0.6),
    inset 0 0 4px rgba(0, 0, 0, 0.4);
}

.timeline-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 50%;
}


.timeline-icon.left {
    left: 100%;
}

.timeline-icon.right {
    left: 0%;
}

.timeline-card {
  background-color: #232326;
  color: white;
  padding: 1.2rem 1.5rem;
  border-radius: 1rem;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.5);
  z-index: 2;
  width: 100%;
}

.timeline-card h3 {
  margin: 0;
  font-size: 1.1rem;
}

.timeline-card .date {
  font-size: 0.9rem;
  color: #aaa;
  display: block;
  margin: 0.3rem 0 0.7rem;
}

.timeline-card p {
  color: #ccc;
  line-height: 1.6;
  margin: 0;
  font-size: 1.05rem;
  line-height: 1.7;
}


/* Responsive layout */
@media (max-width: 768px) {
  .timeline-wrapper {
    padding-left: 6px;
  }

  .timeline-item {
    width: 100%;
    left: 0 !important;
    text-align: left;
    margin-bottom: 3.5rem;
  }

  .timeline-item.left,
  .timeline-item.right {
    text-align: left;
  }

  .timeline-line {
    left: 30px;
    top: 0;
    bottom: 0;
    transform: none;
  }

  .timeline-icon {
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }

  .timeline-icon.left, 
  .timeline-icon.right {
    left: 0;
  }

  .timeline-card {
    margin-left: 3rem;
    width: calc(100% - 3rem);
  }
}

@media (max-width: 480px) {
  .timeline-card {
    padding: 1rem;
  }

  .timeline-card h3 {
    font-size: 1rem;
  }

  .timeline-card p {
    font-size: 0.85rem;
  }
}
