.projects-section {
  padding: 4rem 2rem;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

.section-title {
  text-align: right;
  font-size: 1.8rem;
  color: white;
  margin-bottom: 0.5rem;
  padding-right: 1rem;
}

.section-divider {
  height: 4px;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto 2rem;
  border-radius: 999px;
  background: linear-gradient(to right, #9c27ff 80%, #f39c12 20%);
}

.project-tabs {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.project-tabs button {
  padding: 0.6rem 1.5rem;
  border-radius: 999px;
  background: #2b2b2f;
  color: white;
  border: none;
  font-weight: 600;
  box-shadow:
    0 8px 18px rgba(0, 0, 0, 0.6),
    inset 0 2px 4px rgba(255, 255, 255, 0.04),
    inset 0 -2px 4px rgba(0, 0, 0, 0.4);
}

.project-tabs .active {
  background-color: #9c27ff;
}

.project-container {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
}

.thumbnail-list {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  justify-content: center;
}

.thumbnail {
  width: 64px;
  height: 64px;
  object-fit: cover;
  background: #28282c;
  border-radius: 30%;
  border: 2px solid transparent;
  transition: transform 0.3s ease;
  cursor: pointer;
  padding: 15px;
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.6), inset 0 1px 2px rgba(255, 255, 255, 0.04), inset 0 -1px 2px rgba(0, 0, 0, 0.4);
}

.thumbnail.active {
  transform: scale(1.1);
  border-color: #9c27ff;
}

.project-card {
  background-color: #28282c;
  padding: 2rem;
  border-radius: 16px;
  flex: 1;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.4);
  text-align: center;
}

.project-main-img {
  width: 100%;
  max-width: 700px;
  height: auto;
  border-radius: 12px;
  margin-bottom: 1rem;
}

.project-icons {
  display: flex;
  align-items: center;
  justify-content: center;
}

.project-link {
  padding-left: 8px;
}

@media screen and (max-width: 493px) {
  .thumbnail {
  width: 45px;
  height: 45px;
  object-fit: cover;
  border-radius: 30%;
  border: 2px solid transparent;
  transition: transform 0.3s ease;
  cursor: pointer;
  padding: 8px;
  box-shadow: 0 0 10px #5d6063;
}

.project-container{
  gap: 1.2rem;
}
}