.contact-section {
  text-align: center;
  padding: 2rem;
  max-width: 1100px;
  margin: 0 auto;
  position: relative;
}

.contact-title {
  font-size: 2rem;
  font-weight: 700;
  color: white;
  text-align: right;
  padding-right: 1rem;
}

.section-divider {
  height: 4px;
  width: 100%;
  max-width: 900px;
  margin: 0 auto 3rem;
  border-radius: 999px;
  background: linear-gradient(to right, #9c27ff 80%, #f39c12 20%);
}

.contact-subheading {
  font-size: 1.2rem;
  color: #9c27ff;
  font-weight: 500;
  margin-bottom: 0.3rem;
  text-transform: uppercase;
}

.contact-heading {
  font-size: 2.5rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 1rem;
}

.contact-text {
  font-size: 1.1rem;
  color: #ccc;
  line-height: 1.7;
  margin-bottom: 2.5rem;
  max-width: 600px;
  margin-inline: auto;
}

.contact-button {
  display: inline-block;
  padding: 0.8rem 2.2rem;
  background-color: #9c27ff;
  color: white;
  font-weight: 600;
  border-radius: 999px;
  text-decoration: none;
  box-shadow:
    0 12px 22px rgba(0, 0, 0, 0.7),
    inset 0 2px 6px rgba(255, 255, 255, 0.05),
    inset 0 -2px 6px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
}

.contact-button:hover {
  background-color: #b240ff;
  transform: scale(1.05);
}

.contact-socials {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 3rem;
}

.contact-socials a {
  font-size: 1.8rem;
  color: #ccc;
  background: #28282c;
  padding: .7rem .7rem .4rem;
  border-radius: 50%;
  transition: 0.3s ease;
  box-shadow:
    0 10px 18px rgba(0, 0, 0, 0.6),
    inset 0 1px 2px rgba(255, 255, 255, 0.04),
    inset 0 -1px 2px rgba(0, 0, 0, 0.4);
  border: 2px solid transparent;
}

.contact-socials a:hover {
  border: 2px solid #9c27ff;
  transform: translateY(-1px);
}
