.skills-section {
  padding: 5rem 2rem;
  max-width: 1100px;
  margin: 0 auto;
}

.skills-title {
  text-align: right;
  font-size: 1.8rem;
  color: white;
  margin-bottom: 0.5rem;
  padding-right: 1rem;
}

.section-divider {
  height: 4px;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto 3rem;
  border-radius: 999px;
  background: linear-gradient(to right, #9c27ff 80%, #f39c12 20%);
}

.skills-tabs {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.skills-tabs button {
  padding: 0.6rem 1.6rem;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: 600;
  background-color: #2b2b2f;
  border: none;
  color: white;
  cursor: pointer;
  box-shadow:
    0 8px 18px rgba(0, 0, 0, 0.6),
    inset 0 2px 4px rgba(255, 255, 255, 0.04),
    inset 0 -2px 4px rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease;
}

.skills-tabs button:hover {
  background-color: #3a3a3e;
  transform: translateY(-2px);
}

.skills-tabs .active {
  background-color: #9c27ff;
  color: #fff;
  transform: translateY(-2px);
  box-shadow:
      0 12px 22px rgba(0, 0, 0, 0.8),
      inset 0 2px 6px rgba(255, 255, 255, 0.05),
      inset 0 -2px 6px rgba(0, 0, 0, 0.5);
}

.skills-swiper {
  padding: 2rem 0;
  position: relative;
  margin: 4rem;
}

.swiper-slide {
  display: flex;
  justify-content: center;
    width: 100px;  /* or whatever fits your design */
}

.skill-card {
  height: auto;
  padding: .6rem;
  border-radius: 12px;
  background-color: #28282c;
  text-align: center;
  transition: all 0.3s ease;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.5);
  align-items: center;
}

.skill-card:hover {
  background-color: #28282c;
  transform: scale(1.05);
}

.skill-card img {
  align-items: center;
  height: 54px;
  width: 119px;
  object-fit: contain;
  background: white;
  border-radius: 10px;
  padding: 2px 4px;
}

.skill-card span {
  color: white;
  font-weight: 500;
  font-size: 0.95rem;
}

.swiper-button-next,
.swiper-button-prev {
  color: white;
  background: #2b2b2f;
  border-radius: 50%;
  padding: 1rem;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.5);
  width: 42px;
  height: 42px;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: 16px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));
  gap: 16px;
}

.skills-swiper.centered .swiper-wrapper {
  justify-content: center;
}

@media screen and (max-width: 486px) {
  .skills-swiper {
    margin: 0;
}

}