/* Base Navbar */
.custom-navbar {
    width: 100%;
    background-color: #1a1a1d;
    padding: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    position: relative;
    z-index: 1000;
}

/* Desktop menu */
.nav-center {
    display: flex;
    gap: 2.5rem;
    list-style: none;
    flex-wrap: wrap;
}

.nav-center li a {
    text-decoration: none;
    color: #fff;
    padding: 0.7rem 2rem;
    background-color: #2b2b2f;
    border-radius: 999px;
    font-size: 1rem;
    font-weight: 600;
    transition: 0.3s ease-in-out;
    box-shadow:
        0 1px 2px rgba(0, 0, 0, 0.6),
        inset 0 2px 4px rgba(255, 255, 255, 0.04),
        inset 0 -2px 4px rgba(0, 0, 0, 0.4);
}

.nav-center li a:hover {
    background-color: #9c27ff;
    color: #fff;
    transform: translateY(-2px);
    box-shadow:
        0 12px 22px rgba(0, 0, 0, 0.8),
        inset 0 2px 6px rgba(255, 255, 255, 0.05),
        inset 0 -2px 6px rgba(0, 0, 0, 0.5);
}

/* Hamburger icon for mobile */
.hamburger-toggle {
    display: none;
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    background-color: #9c27ff;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 4px;
    box-shadow:
        0 12px 22px rgba(0, 0, 0, 0.8),
        inset 0 2px 6px rgba(255, 255, 255, 0.05),
        inset 0 -2px 6px rgba(0, 0, 0, 0.5);
    /* box-shadow: 0 0 20px #9c27ff88; */
    cursor: pointer;
}

.bar {
    width: 24px;
    height: 3px;
    background-color: white;
    border-radius: 2px;
}

/* Mobile menu slide-in */
.mobile-menu {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 80px;
    right: 1rem;
    background-color: #2b2b2f;
    border-radius: 20px;
    padding: 1rem 2rem;
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.8);
    list-style: none;
    gap: 1rem;
    z-index: 999;
}

.mobile-menu li a {
    color: white;
    text-decoration: none;
    font-weight: 600;
    padding: 0.5rem 1rem;
    display: block;
    border-radius: 12px;
    transition: 0.3s ease;
}

.mobile-menu li a:hover {
    background-color: #9c27ff;
}

.show {
    display: flex;
}

/* Hide desktop on mobile */
@media screen and (max-width: 768px) {
    .desktop-menu {
        display: none;
    }

    .hamburger-toggle {
        display: flex;
    }
}