.hero {
    padding: 6rem 1rem 4rem;
    text-align: center;
    color: white;
}

.hero-container {
    max-width: 900px;
    /* was 700px */
    width: 100%;
    padding: 0 2rem;
    margin: 0 auto;
    text-align: center;
}

.profile-img {
  width: 100%;
  max-width: 600px; /* Limit size on large screens */
  height: auto;
  aspect-ratio: 1 / 1; /* Keeps it perfectly square */
  object-fit: cover;
  margin-bottom: 1.5rem;
}

h4 {
    font-size: 1.2rem;
    color: #ccc;
    margin-bottom: 0.5rem;
}

h1 {
    font-size: 2.8rem;
    /* slightly larger */
    font-weight: bold;
    margin: 0.5rem 0;
}

.name {
    color: white;
}

h2 {
    font-size: 1.5rem;
    font-weight: 400;
    color: #ccc;
    margin: 0.3rem 0 1rem;
}

.dynamic-title {
    font-size: 1.6rem;
    color: #ccc;
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    font-weight: 500;
    min-height: 1.5em;
    /* prevent jump */
}

.highlight {
    background: linear-gradient(to right, #a855f7, #9c27ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
}

.cursor {
    color: #9c27ff;
    display: inline-block;
    vertical-align: middle;
    /* Fixes alignment */
    line-height: inherit;
    font-size: 32px;
    padding: 0 0.5px;
}

p {
    font-size: 1.05rem;
    max-width: 800px;
    margin: 0 auto 2rem;
    line-height: 1.7;
    color: #cccccc;
}

.hero-buttons {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}

.btn {
    text-decoration: none;
    color: #fff;
    padding: 0.7rem 2rem;
    background-color: #2b2b2f;
    border-radius: 999px;
    font-size: 1rem;
    font-weight: 600;
    transition: 0.3s ease-in-out;
    box-shadow:
        0 1px 2px rgba(0, 0, 0, 0.6),
        inset 0 2px 4px rgba(255, 255, 255, 0.04),
        inset 0 -2px 4px rgba(0, 0, 0, 0.4);
}

.btn:hover {
    background-color: #9c27ff;
    color: #fff;
    transform: translateY(-2px);
    box-shadow:
        0 12px 22px rgba(0, 0, 0, 0.8),
        inset 0 2px 6px rgba(255, 255, 255, 0.05),
        inset 0 -2px 6px rgba(0, 0, 0, 0.5);
}

.hero-container {
    animation: fadeInUp 1s ease-out forwards;
    opacity: 0;
}

@keyframes fadeInUp {
    from {
        transform: translateY(20px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@media (max-width: 400px) {
  .profile-img {
    max-width: 200px;
  }
}