.site-footer {
  text-align: center;
  padding: 2rem 1rem;
  background-color: #1e1e22;
  color: #aaa;
  font-size: 0.95rem;
  box-shadow:
    0 -8px 24px rgba(0, 0, 0, 0.6),
    inset 0 1px 3px rgba(255, 255, 255, 0.03),
    inset 0 -1px 3px rgba(0, 0, 0, 0.4);
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  margin-top: 5rem;
}

.site-footer p {
  font-weight: 500;
  color: #ddd;
  margin-bottom: 0.5rem;
}

.site-footer small {
  font-size: 0.85rem;
  color: #777;
}
