.about-section {
    padding: 5rem 2rem;
    color: white;
}

.about-container {
    max-width: 1100px;
    margin: 0 auto;
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

/* Use consistent height control on both sides */
.about-img {
    width: 320px;
    height: 320px;
    border-radius: 2rem;
    object-fit: cover;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.5);
}

.about-text {
    flex: 1;
    min-width: 300px;
    flex-direction: column;
    justify-content: center;
    height: 320px;
    /* Match image height */
    padding: 1.4rem 0;
}

.about-text h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #fff;
}

.about-text p {
    font-size: 1.05rem;
    color: #cccccc;
    line-height: 1.7;
}

.about-section {
    padding: 6rem 2rem;
    scroll-margin-top: 100px;
    /* This offsets scroll to show the full section */
}

@media (max-width: 768px) {
    .about-container {
        flex-direction: column;
        align-items: center;
    }

    .about-text {
        height: auto;
        text-align: center;
        margin-top: 1rem;
    }
}